import React, {useState, useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { getAllDiets, createRecipe } from "../../redux/actions/actions";
import { Link } from 'react-router-dom';


const CreateRecipe = () => {
    const dispatch = useDispatch()
    const diets = useSelector((state) => state.diets)

    const [newRecipe, setNewRecipe] = useState ({
        name: '',
        summary: '',
        healthScore: '',
        steps: [],
        diets: [],
    })

    const addDiet = function (diet) {
        setNewRecipe({
            ...newRecipe,
            diets: newRecipe.diets.push(diet.name),
        })
    }


    useEffect(() => {
        dispatch(getAllDiets());
    }, []);

    return (
        <div>
            <Link to='/home'><button>Volver</button></Link>

            <h1>Cre√° tu personaje</h1>

            <form>
                <div>
                    <label>Nombre: </label>
                    <input type="text" value={newRecipe.name} name='name'/>
                </div>
                <div>
                    <label>Resumen: </label>
                    <input type="text" value={newRecipe.summary} name='summary'/>
                </div>
                <div>
                    <label>HealthScore: </label>
                    <input type="number" value={newRecipe.healthScore} name='healthScore'/>
                </div>
                <div>
                    <label>Pasos: </label>
                    <input type="text" value={newRecipe.steps} name='steps'/>
                </div>
                <div>
                    <label>Dietas: </label>
                    <select onChange={(diet) => addDiet(diet)}>
                            {diets.map(diet =>
                                <option key={diet.id} value={diet.name} >
                                    {diet.name}
                                </option>
                            )}
                    </select> 
                </div>

            </form>
            
        </div>
    );
}

export default CreateRecipe;
